import React from "react";
import { InfoContainer, InfoWrapper, InfoRow, Column1, TextWrap, TopLine, Heading, Subtitle, Column2, ImgWrap, Img, BtnWrap } from "./infoStyled";
import { Button } from "../buttonElement";

const InfoGerais = ({ lightBg, id, imgStart, lightText, darkText, topLine, headline, description, img, alt, primary, dark, dark2, buttonLabel }) => {
    return (
        <InfoContainer lightBg={lightBg} id={id} >
            <InfoWrapper>
                <InfoRow imgStart={imgStart}>
                    <Column1>
                        <TextWrap>
                            <TopLine>{topLine}</TopLine>
                            <Heading lightText={lightText}>{headline}</Heading>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            <BtnWrap>
                                <Button to='certificacao'
                                    smooth={true}
                                    duration={500}
                                    spy={true}
                                    exact="true"
                                    offset={-80}
                                    primary={primary ? 1 : 0}
                                    dark={dark ? 1 : 0}
                                    dark2={dark2 ? 1 : 0}
                                    >
                                    {buttonLabel}
                                </Button>
                            </BtnWrap>
                        </TextWrap>
                    </Column1>
                    <Column2>
                        <ImgWrap>
                            <Img src={img} alt={alt} />
                        </ImgWrap>
                    </Column2>
                </InfoRow>
            </InfoWrapper>
        </InfoContainer>

    )
}

export default InfoGerais;